body {
  background-color: #fefcf7;
}

main {
  margin-top: 90px;
}

.container {
  width: min(90%, 1280px);
  margin: 0 auto;
}

a {
  text-decoration: none;
}

body {
  line-height: 1.2;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Fraunces", serif;
  font-weight: 900;
}

p {
  font-family: "Barlow";
  line-height: 1.8;
  color: #83888f;
  font-size: 1rem;
}

.nav .nav__links--item__link {
  font-family: "Fraunces", serif;
  font-weight: bold;
  color: #333d4b;
  font-size: 1.5rem;
}

.header__content--title {
  color: #fefcf7;
  font-size: 2.5rem;
}

.products__title {
  color: #333d4b;
  font-size: 2.5rem;
}
@supports (-webkit-background-clip: text) {
  .products__title {
    background: linear-gradient(#83888f, #fefcf7);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
.products__presentation--card__title {
  color: #333d4b;
  font-size: 1.5rem;
}

.services {
  padding: 4rem 1.5rem;
}
.services__description--title {
  color: #fefcf7;
  font-size: 1.75rem;
}
.services .services__features--card__title {
  color: #fefcf7;
  font-size: 1.5rem;
}

.steps--title {
  color: #83888f;
  font-size: 1.5rem;
}
.steps--content__card--number {
  color: #fdd6ba;
  font-size: 4.5rem;
}
.steps--content__card--title {
  color: #333d4b;
  font-size: 1.75rem;
}

.footer-nav__list--item__link {
  color: #83888f;
  font-size: 0.75rem;
  font-family: "Barlow";
  font-weight: 600;
  text-transform: uppercase;
}

.btn-primary {
  font-size: 1.125rem;
  font-family: "Fraunces";
  color: #fefcf7;
  background-color: #0e8784;
  border-radius: 6px;
  padding: 1rem 2rem;
  transition: 0.4s ease-in-out;
}
.btn-primary:hover {
  transform: translateY(1px) scale(0.99);
  background-color: #66d2cf;
}

.products__presentation--card {
  max-width: 290px;
  margin: 0 auto;
  padding: 0 0.5rem;
}
.products__presentation--card__img {
  max-width: 200px;
  filter: blur(0);
  transition: 0.6s;
}
.products__presentation--card__title {
  margin: 1.5rem auto 1rem;
}

.hidden {
  opacity: 0;
}

.show {
  opacity: 1;
}

.lazy-img {
  filter: blur(20px);
}

.slide-left {
  animation: slideInLeft 1s ease-in-out backwards;
}

.slide-right {
  animation: slideInLeft 1s ease-in-out backwards;
}

.fade-in {
  animation: fadeIn 0.8s 1.1s ease-in-out backwards;
}

.services__features--card {
  padding: 4.5rem 1.75rem 3.125rem;
  background-color: #0e8784;
  border-radius: 8px;
  max-width: 290px;
}
.services__features--card__title {
  margin: 3.5rem auto 1.5rem;
}
.services__features--card__para {
  color: #f4f1eb;
}

.steps--content__card {
  max-width: 330px;
}
.steps--content__card--title {
  margin: 1.5rem auto;
}

.nav.mobile {
  position: fixed;
  top: 0;
  left: 5%;
  width: 90%;
  background-color: #fefcf7;
  height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 50;
}
.nav.mobile .nav__img {
  width: min(120px + 8vw, 240px);
}
.nav.mobile .nav__links {
  position: fixed;
  z-index: 20;
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(to bottom, #fefcf7 40%, rgba(255, 255, 255, 0.4));
  top: 90px;
  right: -100%;
  text-align: center;
  opacity: 0;
  height: 0;
  transition: 0.5s cubic-bezier(0.5, 0.7, 0.46, 0.8);
}
.nav.mobile .nav__links--item__link {
  display: block;
  margin-top: 2rem;
  transition: color 0.3s;
}
.nav.mobile .nav__links--item__link:hover {
  color: #0e8784;
}
.nav.mobile ul.nav__links.active {
  opacity: 1;
  height: 100%;
  right: 0;
}
.nav.mobile .menu-icon {
  cursor: pointer;
}

.header {
  position: relative;
  height: max(95vh - 90px, 400px);
}
.header__content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  text-align: center;
  padding: 0 1.5rem;
  max-width: 330px;
}
.header__content--para {
  margin-bottom: 1rem;
}
.header img {
  position: absolute;
  border-radius: 10px;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.products {
  text-align: center;
}
.products__title {
  margin-bottom: 2rem;
}
.products__presentation--card:not(:first-child) {
  margin-top: 3rem;
}

.services {
  position: relative;
  text-align: center;
}
.services ::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 60%;
  background: #333d4b;
  z-index: -1;
  border-radius: 10px;
}
.services__description {
  padding: 4rem 0.5rem;
  max-width: 500px;
  margin: 0 auto;
}
.services__description--title {
  margin-bottom: 1.5rem;
}
.services__description--para {
  color: #f4f1eb;
}

.services__features {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: column;
  gap: 1.5rem;
}

.steps {
  text-align: center;
}
.steps--title {
  margin-bottom: 5rem;
}
.steps--content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3.5rem;
  margin-bottom: 6.4rem;
}

footer {
  text-align: center;
  padding: 3.4rem 0;
  background-color: #333d4b;
}
footer .footer-nav__list {
  margin: 3rem auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
footer .footer-nav__list--item__link {
  transition: all 0.5s;
}
footer .footer-nav__list--item__link:hover {
  color: #0e8784;
}
footer .socials {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  min-width: 120px;
}
footer .socials--item {
  cursor: pointer;
}
footer .socials--item path {
  transition: 0.5s;
}
footer .socials--item:hover path {
  fill: #0e8784;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 7.5rem;
}